(function() {
  'use strict';

  angular
    .module('showPlans', ['ngRoute',  'ngCsvImport','ui.bootstrap','ngAnimate','ngTouch','ngAnimate','ngResource','ngFileUpload']);

})();

angular
  .module('showPlans')
  .service('utilsService', function utilsService($q, $http) {
    this.getPromise = function (httpConfig) {
      var defer = $q.defer();

      $http(httpConfig)
        .success(function () {
          defer.resolve.apply(this, arguments);
        })
        .error(function () {
          defer.reject.apply(this, arguments);
        });

      return defer.promise;
    };
  });

'use strict';

angular.module('showPlans')

  .directive('csvTable', function($http) {
    return {
      restrict: 'E',
      templateUrl:'app/scripts/csvTable.template..html',
      replace: true,
      scope: {
        data: '=',
        header:'='
      },
      controller:function($scope) {
        $scope.columns=[];
        if($scope.header) {
          // could do a little checks here
          for (var k in $scope.data[0]) {
            $scope.columns.push(k);
          }
        }
      }
    };
  });


  'use strict';

  angular
    .module('showPlans')
    .factory('csvService', function ($http, $q, utilsService,config) {
      // Public API here
      return {
        list: function () {
          var def = $q.defer();
          utilsService.getPromise({
            method: 'GET',
            params: {
              'order': '-createdAt'
            },
            url: config.basePath+'/classes/csv'
          }).then(function (data){
            def.resolve(fromMongo(data));
          },function(erro) {
            def.reject(erro);
          });
          return def.promise;
        },

        get: function (id) {
          return utilsService.getPromise({
            method: 'GET',
            url: config.basePath+'/classes/csv/' + id
          });
        },

        update: function (csv) {
          return utilsService.getPromise({
            method: 'PUT',
            data: csv,
            url: config.basePath+'/classes/csv/' + csv.objectId
          });
        },

        remove: function (objectId) {
          console.log('remove');
          console.log(objectId);
          return utilsService.getPromise({
            method: 'DELETE',
            url: config.basePath+'/classes/csv/' + objectId
          });
        },

        create: function (csv) {
          var self = this;
          var defer = $q.defer();
          console.log('create');
          console.log(csv);
          var createPromise = utilsService.getPromise({
            method: 'POST',
            url: config.basePath+'/classes/csv',
            data: toMongo(csv)
          });

          createPromise.then(function (csv) {
            self.get(csv.objectId)
              .then(function () {
                defer.resolve.apply(this, arguments);
              });
          });

          return defer.promise;
        }

      };
      function toMongo(csv) {
        console.log('toMongo');
        console.log(csv);
        var csvObject={};
        if(csv.header) {
          csvObject.header=true;
        } else {
          csvObject.header=false;
        }
        csvObject.data=JSON.stringify(angular.copy(csv.data));
        console.log(csvObject);
        return csvObject;
      }
      function fromMongo(csv) {
        console.log('fmMongo');
        console.log(csv);
        var csvArr=[];
        for(var i=0;i<csv.results.length;i++) {
          var current=csv.results[i];
          try {
            csvArr.push({
              id:current.objectId,
              header:current.header,
              data:JSON.parse(current.data),
              createdAt:current.createdAt
            });
          } catch (e) {
            console.log('ERROIN');
            console.log(e);
          }
        }
        return csvArr;
      }
    });



(function () {
  'use strict';

  angular
    .module('showPlans')
    .controller('ModalInstanceCtrl', ModalInstanceCtrl);

  function ModalInstanceCtrl($scope, $uibModalInstance, csvData,header) {
    $scope.csvData = csvData;
    $scope.header=header;

    $scope.ok = function () {
      $uibModalInstance.close({data:csvData,header:header});
    };

    $scope.cancel = function () {
      $uibModalInstance.dismiss('cancel');
    };
  }
})();

(function () {
  'use strict';

  angular
    .module('showPlans')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($scope, $uibModal, $timeout, csvService, Upload, config) {
    var vm = this;

    vm.showError = false;
    vm.ErrorMessage = "";

    vm.tableLoaded = false;

    $scope.csv = {
      content: null,
      header: false,
      headerVisible: true,
      separator: ',',
      separatorVisible: true,
      result: null,
      encoding: 'ISO-8859-1',
      encodingVisible: true,
      accept: ".csv",
      acceptSize: 2048,
      callback: CSVCallback,
      exCeedFunc: exCeedFunc
    };

    function renderTables() {

      csvService.list()
        .then(function (tasks) {
          console.log('tasks');
          console.log(tasks);
          vm.tables = tasks;
          console.log('vm.tables');
          console.log(vm.tables);
          $timeout(function () {
            // some shit for cloaking

          });

        });
    }

    renderTables();


    function exCeedFunc() {
      $scope.$apply(function () {
        vm.ErrorMessage = "Maximum file size: " + $scope.csv.acceptSize + "Kb";
        vm.showError = true;
      });

      $timeout(function () {
        vm.showError = false;
        console.log('update with timeout fired');
      }, 10000);
    }

    function CSVCallback() {
      openModal();
    }

    function openModal() {
      var modalInstance = $uibModal.open({
        animation: $scope.animationsEnabled,
        ariaLabelledBy: 'modal-title',
        ariaDescribedBy: 'modal-body',
        templateUrl: 'app/partials/modal.template.html',
        controller: 'ModalInstanceCtrl',
        size: 'lg',
        resolve: {
          header: function () {
            return $scope.csv.header;
          },
          csvData: function () {
            return $scope.csv.result;
          }
        }
      });

      modalInstance.result.then(function (csvData) {

        console.log(csvData);
        csvService.create(csvData)
          .then(function (tasks) {
            renderTables();
          });
      }, function () {
        console.log('Modal dismissed at: ' + new Date());
      });
    };

    function deleteTable(id) {
      csvService.remove(id).then(
        function (success) {
          renderTables();
        }, function (error) {
          console.log(error);
        }
      )
    }

  }
})();

